body {
  background-image: url(/static/media/background.7d117529.png);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

*::-webkit-scrollbar {
  width: 0.4em; }

*::-webkit-scrollbar {
  width: 0.4em; }

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0); }

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
  outline: 1px solid slategrey; }

.page {
  margin: 0px;
  max-width: 100%;
  overflow: hidden; }

.container {
  display: block; }

.title {
  background-color: #27ade3;
  color: white;
  font-size: 26px;
  padding: 7px;
  padding-left: 20px;
  text-align: left; }

.subTitle {
  display: flex;
  color: gray;
  margin-top: -10px; }

.place {
  width: 50%;
  text-align: left; }

.charge {
  display: flex;
  font-size: 24px;
  margin: 10px 0px; }

.time {
  width: 50%;
  text-align: right; }

.link {
  text-decoration: none;
  color: black; }

.description {
  margin-top: 0px;
  margin-bottom: 5px;
  text-align: justify; }

